import React, {useEffect, useState} from 'react';
import {Link} from 'react-router-dom';
import SubjectService from '../services/SubjectService'

const ListSubjectComponent = () => {
    const [subjects, setSubjects] = useState([]);
    const [check, setCheck] = useState(true);
    const [ids, setIds] = useState([]);
    useEffect(() => {
        getAllSubjects();
    }, [])


    const getAllSubjects = () => {
        SubjectService.getAllSubjects().then((res) => {
            setSubjects(res.data);
        }).catch(error => console.log(error))
    }
    const handleDelete = (ids) => {
        SubjectService.deleteSubject(ids).then(() => {
            getAllSubjects();
        }).catch(error => console.error(error))
    }

    const checkIds = (ids) => {
        if (ids.length === 0) {
            setCheck(true);
        }
        else {
            setCheck(false);
        }
    }

    return (
        <div className='container'>
            <div>
                <h2 className='text-center'> Danh sch cc mn hc</h2>
                <Link to={"/add-subject"} className="btn btn-primary mb-2">Thm mn hc</Link>
                <table className="table table-bordered border-primary">
                    <thead>
                    <tr>
                        <th>M mn hc</th>
                        <th>Tn mn hc</th>
                        <th>S tn ch</th>
                        <th>Nhm</th>
                        <th>Cp nht</th>
                        <th>
                            <button
                                className='btn btn-info'
                                onClick={() => {
                                    handleDelete(ids);
                                    setCheck(true);
                                    setIds([])
                                }}
                                disabled={check}
                            >Xa
                            </button>
                        </th>

                    </tr>
                    </thead>
                    <tbody>
                    {subjects && subjects.map(subject => {
                        return (
                            <tr key={subject.id}>
                                <td>
                                    {subject.subjectId}
                                </td>
                                <td>
                                    {subject.subjectName}
                                </td>
                                <td>
                                    {subject.credit}
                                </td>
                                <td>
                                    {subject.groupCode}
                                </td>
                                <td>
                                    <Link className='btn btn-info' to={`/edit-subject/${subject.id}`}>Cp nht</Link>
                                </td>
                                <td>
                                    <div className='form-check'>
                                        <input
                                            className='form-check-input'
                                            type={"checkbox"}
                                            value={subject.id}
                                            onChange={(e) => {
                                                if (ids.includes(Number(e.target.value))) {
                                                    for (let i = 0; i < ids.length; i++) {
                                                        if (ids[i] === Number(e.target.value)) {
                                                            ids.splice(i, 1);
                                                            break;
                                                        }
                                                    }
                                                } else {
                                                    ids.push(Number(e.target.value));
                                                }
                                                setIds(ids);
                                                checkIds(ids);
                                            }}

                                        />
                                    </div>
                                </td>
                            </tr>
                        )
                    })}
                    </tbody>
                </table>
            </div>
        </div>
    )


}
export default ListSubjectComponent