import SystemConstant from "../constant/SystemConstant";
import Token from "./Token";
import axios from "axios";

class ActionService {

    getListResponseOfOneRequestOfStudentCurrent() {
        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: `${SystemConstant.BASE_REST_API_URL}/student/subject/rp/by`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${Token.info.token}`
            },
        };
        return axios.request(config);
    }

    getListResponseByUsernameCurrentAndSubjectId(subjectId) {
        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: `${SystemConstant.BASE_REST_API_URL}/student/subject/rp/${subjectId}`,
            headers: {
                'Authorization': `Bearer ${Token.info.token}`
            }
        };
        return axios.request(config)

    }

    getListRequestByUsernameCurrent() {
        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: `${SystemConstant.BASE_REST_API_URL}/student/subject/rq/cr`,
            headers: {
                'Authorization': `Bearer ${Token.info.token}`
            }
        };
        return axios.request(config);
    }
    confirm(requestId, responseId) {
        let config = {
            method: 'put',
            maxBodyLength: Infinity,
            url: `${SystemConstant.BASE_REST_API_URL}/student/subject/rq/cf/${requestId}/${responseId}`,
            headers: {
                'Authorization': `Bearer ${Token.info.token}`
            }
        };
        return axios.request(config);
    }

}
export default new ActionService();