
import axios from "axios";
const MAJOR_BASE_REST_API_URL = 'http://localhost:9191/api/majors';
import SystemConstant from '../constant/SystemConstant'
class MajorSerivce {


    getAllMajors() {
        return axios.get(MAJOR_BASE_REST_API_URL);
    }
    deleteMajor(ids) {
        // const axios = require('axios');
        let data = JSON.stringify(ids);

        let config = {
            method: 'delete',
            maxBodyLength: Infinity,
            url: `${SystemConstant.BASE_REST_API_URL}/majors`,
            headers: {
                'Content-Type': 'application/json'
            },
            data: data
        };

        return axios.request(config)
    }
    updateMajor(major, id) {
        return axios.put(SystemConstant.BASE_REST_API_URL + "/majors/" + id, major);

    }
    createMajor(major) {
        return axios.post(SystemConstant.BASE_REST_API_URL + "/majors", major);

    }
    getMajorById(id) {
        return axios.get(SystemConstant.BASE_REST_API_URL + "/majors/" + id);
    }
}
export default new MajorSerivce();