import axios from "axios";
import Token from "./Token";
import SystemConstant from '../constant/SystemConstant'
class ExchangeSubject {


    request(id) {
        let config = {
            method: 'post',
            maxBodyLength: Infinity,
            url: `${SystemConstant.BASE_REST_API_URL}/student/subject/rq/${id}`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${Token.info.token}` 
            },
        };
        return axios.request(config);
    }

    getListRequest() {
        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: `${SystemConstant.BASE_REST_API_URL}/student/subject/rq/`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${Token.info.token}`
            },
        };
        return axios.request(config);
    }


    exchangeUpdate(dataParam, dataBody) {
        let config = {
            method: 'put',
            maxBodyLength: Infinity,
            url: `http://localhost:9191/api/register/subject/${dataParam.username}/${dataParam.subjectId}`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${Token.info.token}` 
            },
            data: JSON.stringify(dataBody)
        };
        return axios.request(config);
    }
    updateExchangeRecord(id) {
        let config = {
            method: 'put',
            maxBodyLength: Infinity,
            url: `http://localhost:9191/api/register/subject/${id}`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${Token.info.token}` 
            }
        };
        return axios.request(config);
    }

    delete(id) {
        let config = {
            method: 'delete',
            maxBodyLength: Infinity,
            url: `http://localhost:9191/api/register/subject/${id}`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${Token.info.token}`
            }
        };
        return axios.request(config);
    }
    response(studentRequestId) {
        let config = {
            method: 'post',
            maxBodyLength: Infinity,
            url: `${SystemConstant.BASE_REST_API_URL}/student/subject/rp/${studentRequestId}`,
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${Token.info.token}`
            }
        };
        return axios.request(config);
    }
}
export default new ExchangeSubject();