import axios from "axios";
import Token from "./Token";
import SystemConstant from '../constant/SystemConstant'
const headers = {
    'Authorization': `Bearer ${Token.info.token}`
}

const requets = (method, tartgetUrl, param) =>  {
    let config = {
        method: method,
        maxBodyLength: Infinity,
        url: tartgetUrl + param,
        headers: headers
      };
    return axios.request(config);
}



class RegisterSubjectService {

    registerSubjectOfStudent(subjectId, groupId) {
        return requets('post', SystemConstant.BASE_REST_API_URL, `/register-subject/${subjectId}/${groupId}`)
    }

    removeRecordOfStudent(id) {
        return requets('delete', SystemConstant.BASE_REST_API_URL, `/register-subject/${id}`)
    }
    getAllRecordOfStudent() {
        return requets('get', SystemConstant.BASE_REST_API_URL, '/register-subject')
    }
    getInfoDetailsStudentAndSubject() {
        return requets('get', SystemConstant.BASE_REST_API_URL, '/entity/view/student/subject/record');
    }

}
export default new RegisterSubjectService()