
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import StudentSerivce from '../services/StudentService'
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
const ListStudentComponent = () => {
    const [students, setStudents] = useState([]);
    const [check, setCheck] = useState(true);
    const [ids, setIds] = useState([]);
    useEffect(() => {
        StudentSerivce.getAllStudents()
            .then(response => {
                setStudents(response.data);
            })
            .catch(err => console.error(err))
    }, [])

    const getAllStudents = () => {
        StudentSerivce.getAllStudents()
            .then(response => {
                setStudents(response.data);
            })
            .catch(err => console.error(err))
    }

    const handleDelete = (ids) =>{
        StudentSerivce.deleteStudent(ids).then(()=> {
            getAllStudents();
        }).catch(error => console.error(error))
    }

    const checkIds = (ids) => {
        if(ids.length === 0) {
            setCheck(true);   
        }
       else {
        setCheck(false);
       }
    }
   


    return   (
            <div className='container'>
                {students.length === 0 ? (
                    <Backdrop
                        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                        open
                    >
                        <CircularProgress color="inherit" />
                    </Backdrop>
                    ) :
                    (
                    <div>
                        <h2 className='text-center'> List student</h2>
                        <Link to={"/add-student"} className="btn btn-primary mb-2">Add Student</Link>
                        <table className="table table-bordered border-primary">
                            <thead>
                            <tr>
                                <th>M sinh vin</th>
                                <th>H v tn</th>
                                <th>a ch</th>
                                <th>Cp nht</th>
                                <th><button
                                    className='btn btn-info'
                                    onClick = {() => {handleDelete(ids);
                                        setCheck(true);
                                        setIds([])
                                    }}
                                    disabled = {check}
                                >Xa</button></th>

                            </tr>
                            </thead>
                            <tbody>
                            {students && students.map(student => {
                                return (
                                    <tr key={student.id}>
                                        <td>
                                            {student.username}
                                        </td>
                                        <td>
                                            {student.fullName}
                                        </td>
                                        <td>
                                            {student.address}
                                        </td>
                                        <td>
                                            <Link className='btn btn-info' to={`/edit-student/${student.id}`}>Cp nht</Link>
                                        </td>
                                        <td>
                                            <div className='form-check'>
                                                <input
                                                    className='form-check-input'
                                                    type={"checkbox"}
                                                    value = {student.id}
                                                    onChange = {(e)=> {
                                                        if(ids.includes(Number(e.target.value))) {
                                                            for(let i = 0; i < ids.length; i++) {
                                                                if(ids[i] === Number(e.target.value)) {
                                                                    ids.splice(i, 1);
                                                                    break;
                                                                }
                                                            }
                                                        } else {
                                                            ids.push(Number(e.target.value));
                                                        }
                                                        setIds(ids);
                                                        checkIds(ids);
                                                    }}

                                                />
                                            </div>
                                        </td>
                                    </tr>
                                )
                            })}
                            </tbody>
                        </table>
                    </div>
                )}
            </div>
        )
}
export default ListStudentComponent;