import * as React from 'react';
import {styled} from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import ActionService from '../services/ActionService'
import TableResponse from "./TableResponse";

const BootstrapDialog = styled(Dialog)(({theme}) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

function ListResponseOfOneRequest(param) {
    const [open, setOpen] = React.useState(false);
    const [listResponses, setListResponses] = React.useState([]);
    React.useEffect(() => {
        ActionService.getListResponseByUsernameCurrentAndSubjectId(param.subjectId)
            .then((res) => {
                console.log(res.data)
                setListResponses(res.data)
            }).catch(err => {
                console.error(err)
        })
    }, [])


    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>

            <button
                className={'btn btn-primary'}
                onClick={handleClickOpen}
            >Xem cc yu cu
            </button>
            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={open}
                maxWidth = 'xl'

            >
                <DialogTitle sx={{m: 0, p: 2}} id="customized-dialog-title">
                    Danh sch cc sinh vin gi yu cu
                </DialogTitle>
                <IconButton
                    aria-label="close"
                    onClick={handleClose}
                    sx={{
                        position: 'absolute',
                        right: 8,
                        top: 8,
                        color: (theme) => theme.palette.grey[500],
                    }}
                >
                    <CloseIcon/>
                </IconButton>
                <DialogContent dividers>
                    <TableResponse
                        listResponse = {listResponses}
                        requestId = {param.requestId}
                    />
                </DialogContent>

            </BootstrapDialog>
        </div>
    );
}

export default ListResponseOfOneRequest;
