import React, {useEffect, useState} from 'react';
import ActionService from "../services/ActionService";
import ListResponseOfOneRequest from "./ListResponseOfOneRequest";
import Backdrop from "@mui/material/Backdrop/Backdrop";
import CircularProgress from "@mui/material/CircularProgress/CircularProgress";

function ListRequestExchangeSubject() {

    const [request, setRequest] = useState([])

    useEffect(() => {
        getAllRequestOfStudentCurrent();
    }, [])


    const getAllRequestOfStudentCurrent = () => {
        ActionService.getListRequestByUsernameCurrent()
            .then(res => {
                setRequest(res.data)
            })
            .catch((err) => console.error(err))
    }

    const handleConfirm = (e) => {

    }

    return (
        <div className="container">
            {request.length === 0 ? (
                    <Backdrop
                        sx={{color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1}}
                        open
                    >
                        <CircularProgress color="inherit"/>
                    </Backdrop>
                )
                :
                (
                    <div>
                        <div className="table">
                            <h2 className='text-center'> Mn hc ang i</h2>
                            <table className="table table-bordered">
                                <thead>
                                <tr>
                                    <th>Tn mn hc</th>
                                    <th>M mn hc</th>
                                    <th>S tn ch</th>
                                    <th>Nhm</th>
                                    <th>Ngy hc</th>
                                    <th>Hnh ng</th>
                                </tr>
                                </thead>
                                <tbody>
                                {request && request.map(request => {
                                    return (
                                        <tr key={request.id}
                                        >
                                            <td>{request.subjectDTO.subjectName}</td>
                                            <td>{request.subjectDTO.subjectCode}</td>
                                            <td>{request.subjectDTO.credit}</td>
                                            <td>{request.groupDTO.code}</td>
                                            <td>{request.subjectDTO.startDate} - {request.subjectDTO.endDate}</td>
                                            <td>
                                                <ListResponseOfOneRequest
                                                    subjectId={request.subjectDTO.id}
                                                    requestId={request.id}
                                                />
                                            </td>
                                        </tr>
                                    )
                                })}
                                </tbody>
                            </table>
                        </div>
                    </div>
                )
            }

        </div>
    )
}

export default ListRequestExchangeSubject;