import './App.css';
import {BrowserRouter as Router, Route, Routes} from 'react-router-dom';
import FooterComponent from './components/FooterComponent';
import HeaderComponent from './components/HeaderComponent';
import ListStudentComponent from './components/ListStudentComponent';
import AddStudentComponent from './components/AddStudentComponent';
import ListMajorComponent from './components/ListMajorComponent';
import AddMajorComponent from './components/AddMajorComponent';
import ListSubjectComponent from './components/ListSubjectComponent'
import AddSubjectComponent from './components/AddSubjectComponent';
import LoginComponent from './components/LoginComponent';
import RegisterSubjectComponent from './components/RegisterSubjectComponent';
import ExchangeSubject from './components/ExchangeSubject';
import ListRequestExchangeSubject from './components/ListRequestExchangeSubject';
function App() {
    return (
        <div>
            <Router>
                <HeaderComponent/>
                <div className="container">
                    <Routes>
                        <Route exact path="/" element={<ListStudentComponent/>}/>
                        <Route exact path="/trang-chu" element={<ListStudentComponent/>}/>
                        <Route path="/students" element={<ListStudentComponent/>}/>
                        <Route path="/add-student" element={<AddStudentComponent/>}/>
                        <Route path="/edit-student/:id" element={<AddStudentComponent/>}/>
                        <Route path="/majors" element={<ListMajorComponent/>}/>
                        <Route path="/add-major" element={<AddMajorComponent/>}/>
                        <Route path="/edit-major/:id" element={<AddMajorComponent/>}/>
                        <Route path="/subjects" element={<ListSubjectComponent/>}/>
                        <Route path="/edit-subject/:id" element={<AddSubjectComponent/>}/>
                        <Route path="/add-subject" element={<AddSubjectComponent/>}/>
                        <Route path="/dang-nhap" element={<LoginComponent/>}/>
                        <Route path="/dang-ky-mon-hoc" element={<RegisterSubjectComponent/>}/>
                        <Route path="/trao-doi-mon-hoc" element={<ExchangeSubject/>}/>
                        <Route path="/thong-tin-trao-doi-mon-hoc" element={<ListRequestExchangeSubject/>}/>

                    </Routes>
                </div>
            </Router>
        </div>
    );
}

export default App;