import React, {useEffect, useState} from 'react';
import ExchangeSubjectService from '../services/ExchangeSubjectService';
import RegisterService from '../services/RegisterSubjectService'
import Backdrop from "@mui/material/Backdrop/Backdrop";
import CircularProgress from "@mui/material/CircularProgress/CircularProgress";

function ExchangeSubject() {

    const [subjectRegisters, setSubjectRegisters] = useState([]);
    const [listRequest, setListRequest] = useState([])


    useEffect(() => {
        getAllRegisterOfStudent()
        getListRequestOfStudent()

    }, [])


    const getAllRegisterOfStudent = () => {
        RegisterService.getAllRecordOfStudent()
            .then((res) => {
                setSubjectRegisters(res.data)
            }).catch((err) => {
            console.error(err);
        });
    }
    const getListRequestOfStudent = () => {
        ExchangeSubjectService.getListRequest()
            .then((res) => {
                setListRequest(res.data)
            }).catch((e) => {
            console.error(e)
        })
    }

    const handleResponse = (e) => {
        const studentRequestId = e.target.value;
        ExchangeSubjectService.response(studentRequestId)
            .then(() => {
                alert("Success!")
            }).catch((e) => {
            console.error(e)
        })
    }


    const handleRequest = (e) => {
        const registerId = e.target.value;
        ExchangeSubjectService.request(registerId).then(() => {
            getAllRegisterOfStudent()
            getListRequestOfStudent()
        }).catch((e) => console.error(e))
    }


    // const handleDelete =  (e) => {
    //     ExchangeSubjectService.delete(e.target.value)
    //         .then(() => {
    //             alert(" hy")
    //             getAllRecordSubjectState()
    //             getViewSubjectExchange()
    //         }).catch(() => alert("Li"))
    // }

    return (
        <div>
            { listRequest.length === 0?
                (
                    <Backdrop
                        sx={{color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1}}
                        open
                    >
                        <CircularProgress color="inherit"/>
                    </Backdrop>
                )
                :
                (
                    <div>
                        <div className='row'>
                            <div className="column ">
                                <div className="table">
                                    <h2 className='text-center'> Mn hc  ng k</h2>
                                    <table className="table table-bordered">
                                        <thead>
                                        <tr>

                                            <th>Tn mn hc</th>
                                            <th>M mn hc</th>
                                            <th>S tn ch</th>
                                            <th>Nhm</th>
                                            <th>Ngy hc</th>
                                            <th>Hnh ng</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {subjectRegisters && subjectRegisters.map(register => {
                                            return (
                                                <tr key={register.id}
                                                >
                                                    <td>{register.subjectDTO.subjectName}</td>
                                                    <td>{register.subjectDTO.subjectCode}</td>
                                                    <td>{register.subjectDTO.credit}</td>
                                                    <td>{register.groupDTO.code}</td>
                                                    <td>{register.subjectDTO.startDate} - {register.subjectDTO.endDate}</td>
                                                    <td>
                                                        <button

                                                            value={register.id}
                                                            onClick={(e) => {
                                                                handleRequest(e);
                                                            }}
                                                            disabled={register.status}
                                                            className={'btn btn-primary'}
                                                        >i
                                                        </button>
                                                        <button

                                                            value={1}
                                                            onClick={(e) => {
                                                                // handleDelete(e);
                                                            }}
                                                            className={'btn btn-danger'}
                                                        >Hy
                                                        </button>
                                                    </td>
                                                </tr>
                                            )
                                        })}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>

                        <div className='row'>
                            <div className='column'>
                                <div className="table">
                                    <h2 className='text-center'> Mn hc ang c trao i</h2>
                                    <table className="table table-bordered">
                                        <thead>
                                        <tr>
                                            <th>H v tn</th>
                                            <th>M sinh vin</th>
                                            <th>Tn mn hc</th>
                                            <th>M mn hc</th>
                                            <th>S tn ch</th>
                                            <th>Nhm</th>
                                            <th>Ngy hc</th>
                                            <th>Hnh ng</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {listRequest && listRequest.map(request => {
                                            let check = false;
                                            for (let i = 0; i < subjectRegisters.length; i++) {
                                                const r = subjectRegisters[i];
                                                if (r.subjectDTO.id === request.subjectDTO.id &&
                                                    r.studentDTO.id === request.studentDTO.id
                                                ) {
                                                    check = true;
                                                    break;
                                                }
                                            }
                                            return (
                                                <tr key={request.id}>
                                                    <td>{request.studentDTO.fullName}</td>
                                                    <th>{request.studentDTO.username}</th>
                                                    <td>{request.subjectDTO.subjectName}</td>
                                                    <td>{request.subjectDTO.subjectCode}</td>
                                                    <td>{request.subjectDTO.credit}</td>
                                                    <td>{request.groupDTO.code}</td>
                                                    <td>{request.subjectDTO.startDate} - {request.subjectDTO.endDate}</td>
                                                    <td>
                                                        <button className='btn btn-primary'
                                                                value={request.id}
                                                                onClick={
                                                                    (e) => {
                                                                        handleResponse(e);
                                                                    }}

                                                                disabled={check}
                                                        >i
                                                        </button>
                                                        <button
                                                            className='btn btn-danger'
                                                            onClick={(e) => {
                                                                // handleCancel(e);
                                                            }}
                                                            // value = {subjectEx.id}
                                                            disabled={!check}
                                                        >
                                                            Hy
                                                        </button>

                                                    </td>
                                                </tr>
                                            )
                                        })}
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>

                    </div>
                )
            }
        </div>
    )


}

export default ExchangeSubject;