import React, {useEffect, useState} from 'react';
import SubjectService from '../services/SubjectService';
import RegisterSubjectService from '../services/RegisterSubjectService';
import {useNavigate} from 'react-router-dom';
import Backdrop from "@mui/material/Backdrop/Backdrop";
import CircularProgress from "@mui/material/CircularProgress/CircularProgress";

function RegisterSubjectComponent() {


    const [subjects, setSubjects] = useState([]);
    const [registers, setRegisters] = useState([]);
    const [subjectSelects, setSubjectSelects] = useState([])
    useEffect(() => {
        SubjectService.getAllSubjects()
            .then(res => {
                setSubjectSelects(res.data)
            }).catch(err => console.error(err));
        getAllSubject()
        getAllSubjectRegisterOfStudent()

    }, [])


    const getAllSubject = () => {
        SubjectService.getAllSubjects()
            .then(res => {
                setSubjects(res.data)
            }).catch(err => console.error(err));

    }

    const getAllSubjectRegisterOfStudent = () => {
        RegisterSubjectService.getAllRecordOfStudent()
            .then((res) => {
                setRegisters(res.data)
            }).catch(() => {
            console.log("Get Subject Record Of Student Failure")
        })
    }


    const handleRegister = (e) => {
        const subject = e.target.value.split(",");
        RegisterSubjectService.registerSubjectOfStudent(subject[0], subject[1])
            .then((res) => {
                getAllSubjectRegisterOfStudent();
            }).catch(() => {
            // alert("Bn  ng k hc phn: " + subject[1])
        });
    }


    const handleDelete = (id) => {
        RegisterSubjectService.removeRecordOfStudent(id)
            .then(() => {
                    getAllSubjectRegisterOfStudent()
                }
            )
            .catch((err) => console.error(err));
    }
    const handleSelectSubject = (e) => {
        const select = e.target.value;
        if (select === 'ALL') {
            getAllSubject()
        } else {
            SubjectService.findById(e.target.value)
                .then((res) => {
                    setSubjects([res.data])
                }).catch((err) => {
                console.error(err)
            })
        }
    }


    return (
        <div className='container'>
            {subjects.length === 0

                ? (
                    (
                        <Backdrop
                            sx={{color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1}}
                            open
                        >
                            <CircularProgress color="inherit"/>
                        </Backdrop>
                    )
                )
                :
                (<div>
                    <h2 className='text-center'> ng k mn hc</h2>
                    <form>
                        <select
                            className="form-select"
                            onChange={(e) => {
                                handleSelectSubject(e)
                            }}
                        >
                            <option value={'ALL'}>Tt c</option>
                            {subjectSelects && subjectSelects.map((subject) => {
                                return (
                                    <option
                                        value={subject.id}
                                        key={subject.id}

                                    >{subject.subjectName}</option>
                                )
                            })}
                        </select>
                    </form>
                    {/* <Link to={"/add-student"} className="btn btn-primary mb-2">Add Student</Link> */}
                    <table className="table table-bordered border-primary">
                        <thead>
                        <tr>
                            <th>M mn hc</th>
                            <th>Tn mn hc</th>
                            <th>Ngy bt u</th>
                            <th>Ngy kt thc</th>
                            <th>S tn ch</th>
                            <th>Nhm</th>
                            <th>ng k</th>

                        </tr>
                        </thead>
                        <tbody>
                        {subjects.map(subject => {
                            // subject.check = false;
                            // subjectRecords.map(subjectRecord => {
                            //     if(subjectRecord.id === subject.id) {
                            //         subject.check = true;
                            //     }
                            // })
                            const SubjectElements = subject.groupId.map((groupId, index) => {
                                return (
                                    (
                                        <tr key={groupId}>
                                            <td>
                                                {subject.subjectCode}
                                            </td>
                                            <td>
                                                {subject.subjectName}
                                            </td>
                                            <td>
                                                {subject.startDate}
                                            </td>
                                            <td>
                                                {subject.endDate}
                                            </td>
                                            <td>
                                                {subject.credit}
                                            </td>
                                            <td>
                                                {subject.groupCode[index]}
                                            </td>
                                            <td>
                                                <div className='form-check'>
                                                    <input
                                                        className='form-check-input'
                                                        style={{
                                                            cursor: 'pointer'
                                                        }}
                                                        checked={subject.check}
                                                        type={"checkbox"}
                                                        value={subject.id + ',' + groupId}
                                                        onChange={(e) => {
                                                            handleRegister(e);
                                                            // checked(e.target.value)
                                                        }}
                                                        disabled={subject.check}

                                                    />
                                                </div>
                                            </td>
                                        </tr>
                                    )
                                )
                            })
                            return SubjectElements

                        })}
                        </tbody>
                    </table>

                    <div className="table">
                        <h2 className='text-center'> ng k thnh cng</h2>
                        <table className="table table-bordered">
                            <thead>
                            <tr>
                                <th>Tn mn hc</th>
                                <th>M mn hc</th>
                                <th>S tn ch</th>
                                <th>Nhm</th>
                                <th>Ngy hc</th>
                                <th>Xa</th>
                            </tr>
                            </thead>
                            <tbody>
                            {registers && registers.map(register => {
                                return (
                                    <tr key={register.id}>
                                        <td>{register.subjectDTO.subjectName}</td>
                                        <td>{register.subjectDTO.subjectCode}</td>
                                        <td>{register.subjectDTO.credit}</td>
                                        <td>{register.groupDTO.code}</td>
                                        <td>{register.subjectDTO.startDate} - {register.subjectDTO.endDate}</td>
                                        <td>
                                            <button
                                                style={{
                                                    border: 'none',
                                                    background: 'none',
                                                    fontWeight: 'bold',
                                                    color: 'red'
                                                }}
                                                onClick={() => {
                                                    handleDelete(register.id)
                                                }}
                                            >X
                                            </button>
                                        </td>
                                    </tr>
                                )
                            })}
                            </tbody>
                        </table>
                    </div>
                </div>)
            }

        </div>

    )
}

export default RegisterSubjectComponent;