import * as React from 'react';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import ActionService from '../services/ActionService'
const columns = [
    { id: 'fullName', label: 'H v tn', minWidth: 170 },
    { id: 'subjectCode', label: 'M mn hc', minWidth: 100 },
    {
        id: 'subjectName',
        label: 'Tn mn hc',
        minWidth: 170,
        align: 'right',
    },
    {
        id: 'credit',
        label: 'S tn ch',
        minWidth: 170,
        align: 'right',
    },
    {
        id: 'group',
        label: 'Nhm hc',
        minWidth: 170,
        align: 'right',
    },
    {
        id: 'time',
        label: 'Thi gian hc',
        minWidth: 170,
        align: 'right',
    },
    {
        id: 'action',
        label: 'Hnh ng',
        minWidth: 170,
        align: 'right',
    }
];

function TableResponse(param) {

    function createData(fullName, subjectCode, subjectName, credit, group, time, action) {
        return { fullName, subjectCode, subjectName, credit, group, time, action };
    }
    const rows  = param.listResponse.map((response) => {
        return createData(
            response.studentResponse.fullName,
            response.subjectResponse.subjectCode,
            response.subjectResponse.subjectName,
            response.subjectResponse.credit,
            response.groupResponse.code,
            new Date(response.subjectResponse.startDate).toLocaleDateString('en-GB') + "  -  " + new Date(response.subjectResponse.endDate).toLocaleDateString('en-GB'),
            <button
                value = {response.id}
                className={"btn btn-primary"}
                onClick={(e) => {
                    handleConfirm(e)
                }}
            >Xc nhn</button>

        )
    })

    const handleConfirm = (e) => {
        const responseId = e.target.value;
        const requestId = param.requestId;
        ActionService.confirm(requestId, responseId)
            .then(() => {
                window.location.reload()
                alert("i thnh cng");
            }).catch((err) => {
                console.error(err)
        })

    }

    return (
        <Paper sx={{ width: '100%', overflow: 'hidden' }}>
            <TableContainer sx={{ maxHeight: 500 }}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align={column.align}
                                    style={{ minWidth: column.minWidth }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows
                            .map((row) => {
                                return (
                                    <TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                                        {columns.map((column) => {
                                            const value = row[column.id];
                                            return (
                                                <TableCell key={column.id} align={column.align}>
                                                    {column.format && typeof value === 'number'
                                                        ? column.format(value)
                                                        : value}
                                                </TableCell>
                                            );
                                        })}
                                    </TableRow>
                                );
                            })}
                    </TableBody>
                </Table>
            </TableContainer>
            {/*<TablePagination*/}
            {/*rowsPerPageOptions={[10, 25, 100]}*/}
            {/*component="div"*/}
            {/*count={rows.length}*/}
            {/*rowsPerPage={rowsPerPage}*/}
            {/*page={page}*/}
            {/*onPageChange={handleChangePage}*/}
            {/*onRowsPerPageChange={handleChangeRowsPerPage}*/}
            {/*/>*/}
        </Paper>
    );
}

export default  TableResponse;