
import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import StudentSerivce from '../services/StudentService'
import MajorService from '../services/MajorService';
const ListMajorComponent = () => {
    const [majors, setMajors] = useState([]);
    const [check, setCheck] = useState(true);
    const [ids, setIds] = useState([]); 

    const getAllMajor = () => {
        MajorService.getAllMajors().then(res => {
            setMajors(res.data);
        }).catch(err => {console.error(err);});
    }

    useEffect(()=> {
        getAllMajor();
    }, [])

    const handleDelete = (ids) =>{
        MajorService.deleteMajor(ids).then(()=> {
            getAllMajor();
        }).catch(err => {console.error(err);});
    }

    const checkIds = (ids) => {
        if(ids.length === 0) {
            setCheck(true);   
        }
       else {
        setCheck(false);
       }
        console.log(check)
    }
   


    return (
        <div className='container'>
            <h2 className='text-center'> Danh sch cc ngnh hc</h2>
            <Link to={"/add-major"} className="btn btn-primary mb-2">Add Major</Link>
            <table className="table table-bordered border-primary">
                <thead>
                    <tr>
                        <th>M Ngnh</th>
                        <th>Tn ngnh</th>
                        <th>Cp nht</th>
                        <th><button 
                            className='btn btn-info' 
                            onClick = {() => {handleDelete(ids); 
                                setCheck(true);
                                setIds([])
                            }}
                            disabled = {check}
                        >Xa</button></th>
                        
                    </tr>
                </thead>
                <tbody>
                    {majors && majors.map(major => {
                        return (
                            <tr key={major.id}>
                                <td>
                                    {major.code}
                                </td>
                                <td>
                                    {major.name}
                                </td>
                                <td>
                                    <Link className='btn btn-info' to={`/edit-major/${major.id}`}>Cp nht</Link>
                                </td>
                                <td>
                                    <div className='form-check'>
                                        <input
                                            className='form-check-input'
                                            type={"checkbox"}
                                            value = {major.id}
                                            onChange = {(e)=> {
                                                if(ids.includes(Number(e.target.value))) {
                                                    for(let i = 0; i < ids.length; i++) {
                                                        if(ids[i] === Number(e.target.value)) {
                                                            ids.splice(i, 1);
                                                            break;
                                                        }
                                                    }
                                                } else {
                                                    ids.push(Number(e.target.value));
                                                }
                                                setIds(ids);
                                                checkIds(ids);
                                            }}
                                            
                                        />
                                    </div>
                                </td>
                            </tr>
                        )
                    })}
                </tbody>
            </table>
        </div>
    )
}
export default ListMajorComponent;