import axios from "axios";
import Token from "./Token";
const SUBJECT_BASE_REST_API_URL = 'http://localhost:9191/api/subjects';

class SubjectService {

    getAllSubjects() {
        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: SUBJECT_BASE_REST_API_URL,
            headers: { 
                'Authorization': `Bearer ${Token.info.token}`
              }
          };
        return axios.request(config);
    }
    createSubject(subject) {
        return axios.post(SUBJECT_BASE_REST_API_URL, subject);
    }
    updateSubject(subject, id) {
        return axios.put(SUBJECT_BASE_REST_API_URL + "/" + id, subject);
    }
    deleteSubject(ids) {
        let data = JSON.stringify(ids);
        let config = {
            method: 'delete',
            maxBodyLength: Infinity,
            url: SUBJECT_BASE_REST_API_URL,
            headers: {
                'Content-Type': 'application/json'
            },
            data: data
        };
        return axios.request(config)
    }
    getSubjectById(id) {
        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: SUBJECT_BASE_REST_API_URL + "/" + id,
            headers: {
                'Authorization': `Bearer ${Token.info.token}`
            }
        };
        return axios.request(config);
    }
    findById(id) {

        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: `http://localhost:9191/api/subjects/${id}` ,
            headers: {
                'Authorization': `Bearer ${Token.info.token}`
            }
        };
        return axios.request(config);
    }
}
export default new SubjectService();