
import axios from 'axios'
import Token from './Token';
const STUDENT_BASE_REST_API_URL = 'http://localhost:9191/api/students';


class StudentService {


    getAllStudents() {
        let config = {
            method: 'get',
            maxBodyLength: Infinity,
            url: STUDENT_BASE_REST_API_URL,
            headers: { 
                'Authorization': `Bearer ${Token.info.token}`
            }
          };
        return axios.request(config);
    }
    createStudent(student) {
        return axios.post(STUDENT_BASE_REST_API_URL, student);
    }
    getStudentById(studentId) {
        return axios.get(STUDENT_BASE_REST_API_URL + '/' + studentId);
    }
    updateStudent(student, studentId) {
        return axios.put(STUDENT_BASE_REST_API_URL + '/' + studentId, student);
    }
    deleteStudent(ids) {
        // const axios = require('axios');
        let data = JSON.stringify(ids);

        let config = {
            method: 'delete',
            maxBodyLength: Infinity,
            url: 'http://localhost:9191/api/students',
            headers: {
                'Content-Type': 'application/json'
            },
            data: data
        };

        return axios.request(config)
            
    }
}
export default new StudentService();